'use strict';
import {EventTarget} from "../common/event";
import {SynchronousJsonPortMarshaler} from "./marshaler";
import {IFileProvider} from "../interfaces/iFileProvider";
import {ThemeMap} from "../common/themeMap";
import {Promise} from "q";
export class ThemeMarshaler extends SynchronousJsonPortMarshaler {
    constructor(control: EventTarget, public fileProvider: IFileProvider, public themeMap: ThemeMap, public pluginCss: string) {
        super(control);
    }
    public getCssFile(url: string): Promise<string> {
        return this.fileProvider.getFileContents(url);
    }
    public fireThemeChangedEvent(themeMap: ThemeMap): void {
        this.themeMap = themeMap;
        var eventArgs = {
            themeMap: themeMap
        };
        this.fireEvent('themechanged', eventArgs);
    }
    public initializeState(): void {
        var eventArgs = {
            themeMap: this.themeMap,
            PluginCss: this.pluginCss
        };
        this.fireEvent('themeinitialize', eventArgs);
    }
}